<template>
  <div id="box2">
    <!-- <div class="top">
      <van-search
        v-model="value"
        placeholder="请输入搜索关键词"
        show-action
        shape="round"
        label="所有设备(888)"
        @search="onSearch"
      >
        <div slot="action" @click="onSearch">搜索</div>
      </van-search>
    </div> -->
    <div class="all">所有设备({{count}})</div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      loading-text="加载中..."
      @load="onLoad">
      <div class="item" v-for="(item, i) in list" :key="i">
        <div class="c6 f14">设备编号: <span class="c13 fw">{{item.number}}</span></div>
        <div class="c6 f14">酒店名称: <span class="c13 fw">{{item.hotel_name}}</span></div>
        <div class="c6 f14">绑定日期: <span class="c13 fw">{{item.time}}</span></div>
        <img src="@/assets/images/my/del.png" alt="" @click="del_shebei(item.id,i)" class="pos_img"/>
      </div>
    </van-list>
  </div>
</template>

<script>
import { getnumber_list, getmy_hotel_detail } from '@/axios/getData'
import { Search, List, Dialog, Toast } from 'vant';
export default {
  data(){
    return{
      page:1,
      list:[],
      count:"",
      loading: false,
      finished: false,
      arrs:''
    }
  },
  components: {
    vanSearch: Search,
    vanList: List
  },
  created() {
    // this.getdata()
    // let arrs = JSON.parse(localStorage.getItem('shebei_arr'));
    // this.arrs = arrs.join(',')
  },
  methods:{
    async onLoad(){
      if(this.loading){
        // var arrs=localStorage.get
        var res = await getmy_hotel_detail({page:this.page,user_id:this.$route.query.user_id});
        // this.list = res.data.list
        this.count = res.data.count
        if(this.page == 1){
          this.list = res.data.list
        } else {
          this.list = this.list.concat(res.data.list)
        }
        this.page++
        this.loading = false
        if(this.list.length>=this.count){
          this.finished =true
        }
        console.log(res.data)
      }
    },
    del_shebei(id,index){
      Dialog.confirm({
        title: '删除提示',
        message: "删除后不可恢复哦"
      }).then(() => {
        getnumber_del({id}).then((res)=>{
          if(res.code==200){
            Toast(res.data);
            this.list.splice(index,1)
          }
        })
      }).catch(() => {
        
      });
    }
  },
  mounted(){

  }
}
</script>

<style scoped lang="scss">
#box2{
  height:100vh;
  background: #FAFAFA;
  margin-top:20px; 
}
.item{
  position: relative;
  padding:20px;
  background: #fff;
  margin-top: 30px;
}
.all{
  text-align: center;
  font-size: 32px;
}
.pos_img{
  position: absolute;
  width:90px;
  height:90px;
  top:50%;
  right:5%;
  transform: translateY(-50%);
}
</style>



